/* Row.css */

.row {
  margin-left: 20px;
  color: white;
}

.row__posters {
  display: flex;
  /* overflow-y: hidden; */
  /* overflow-x: scroll; */
  padding: 20px;
}

.row__poster {
  object-fit: contain;
  width: 100%;
  max-height: 100px;
  /* margin-right: 10px; */
  transition: transform 450ms;
  /* position: relative;  */
  /* z-index: 10; Make sure the poster is above other elements */
}
.row__poster:hover {
  transform: scale(1.08);
  opacity: 1;
}
.row__posterLarge:hover {
  transform: scale(1.09);
  opacity: 1;
}

.row__posterLarge {
  max-height: 250px;
}